<template>
  <div>
    <div class="page-header page-header-small">
      <carousel-section></carousel-section>
    </div>
    <div class="section section-about-us">
        <div class="container-custom bg-about-us">
            <div class="container">
                <div class="row">
                    <div class="col-md-8 ml-auto mr-auto text-center">
                        <h2 class="title pt-10 pb-10 mb-10 text-uppercase">Giới thiệu</h2>
                        <h5 class="description">
                            Là đối tác của các hãng công nghệ không dây hàng đầu như Peplink, Wavetel, Teltonika... Netmark Distribution mang đến những sản phẩm, giải pháp hạ tầng mạng chất lượng cao phù hợp với mọi mô hình và nhu cầu hệ thống.
                        </h5>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="section section-about-us">
        <div class="container-custom">
            <div class="row">
                <div class="col-md-12 ml-auto mr-auto text-center">
                    <h2 class="title text-uppercase">Giải Pháp</h2>
                    <carousel-solution></carousel-solution>
                </div>
            </div>
        </div>
    </div>
      <div class="section">
          <div class="separator"></div>
          <div class="container p-10">
              <div class="row">
                  <h2 class="tab-title" @click="clickTab(1)" v-bind:class="{'tab-active' : tab_active == 1}">Peplink</h2>
                  <h2 class="tab-title" @click="clickTab(2)" v-bind:class="{'tab-active' : tab_active == 2}">Teltonika</h2>
                  <h2 class="tab-title" @click="clickTab(3)" v-bind:class="{'tab-active' : tab_active == 3}">Wavetel</h2>
              </div>
          </div>
          <div class="container" v-if="tab_active == 1">
              <div class="row">
                  <div class="col-md-12 ml-auto mr-auto text-center">
                      <carousel-items></carousel-items>
                  </div>
              </div>
          </div>
          <div class="container" v-if="tab_active == 2">
              <div class="row">
                  <div class="col-md-12 ml-auto mr-auto text-center">
                      <carousel-items2></carousel-items2>
                  </div>
              </div>
          </div>
          <div class="container" v-if="tab_active == 3">
              <div class="row">
                  <h2 class="title"></h2>
                  <div class="col-md-12 ml-auto mr-auto text-center">
                      <carousel-items3></carousel-items3>
                  </div>
              </div>
          </div>
          <div class="container p-10">
              <div class="row">
                  <h2 class="tab-title tab-active">Tin tức</h2>
                  <div class="col-md-12 ml-auto mr-auto text-center">
                      <carousel-items4></carousel-items4>
                  </div>
              </div>
          </div>
      </div>
    <div class="section section-team text-center">
      <div class="container">
        <div class="col-md-3 ml-auto mr-auto text-center">
        <div class="send-button">
          <n-button type="primary" round block size="lg"
          >Xem thêm</n-button
          >
        </div>
        </div>
      </div>
    </div>
    <home-call-ring></home-call-ring>
  </div>
</template>
<script>
import CarouselSection from './components/CarouselSection';
import CarouselSolution from './components/CarouselSolution';
import CarouselItems from '../components/CaroselMultiple/Carosel.vue';
import CarouselItems2 from '../components/CaroselMultiple/Carosel-2.vue';
import CarouselItems3 from '../components/CaroselMultiple/Carosel-3.vue';
import CarouselItems4 from '../components/CaroselMultiple/Carosel-4.vue';
import HomeCallRing from './components/HomeCallRing.vue';
import { Button, FormGroupInput } from '@/components';
export default {
  name: 'landing',
  bodyClass: 'landing-page',
  components: {
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput,
    CarouselSection,
    CarouselSolution,
      CarouselItems,
      CarouselItems2,
      CarouselItems3,
      CarouselItems4,
      HomeCallRing
  },
  data() {
    return {
      form: {
        firstName: '',
        email: '',
        message: '',
      },
      tab_active : 1
    };
  },
    methods : {
      clickTab(id) {
          this.tab_active = id
      }
    }
};
</script>
<style>
  .tab-title{
    font-weight: 600;
    text-transform: uppercase;
    margin: auto;
    cursor: pointer;
  }
  .tab-container {
    margin: auto;
    cursor: pointer;
  }
  .product-inner {
    display:flex !important;
    flex-direction: column !important;
    padding: 5px;
    margin: 5px;
    position: relative;
      -webkit-box-shadow: 0 0 1px #bfbbbb;
      box-shadow: 0 0 1x #bfbbbb;
  }
  .product-img {
    position: relative;
    overflow: hidden;
    border-radius: 3px 3px 0 0;
    height: 200px;
  }
  .product-img img{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform .5s;
  }
  .product-title {
    position: relative;
    text-align: left;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 600;
  }
  .product-desc {
    text-align: left;
  }
  .home-tab h2 {
    font-weight: 600;
    text-transform: uppercase;
  }
  .tab-active {
     border-bottom: solid 2px red;
   }
  .p-10 {
    padding: 10px;
  }
  .product-inner:hover  {
    border: solid 1px #ccc;
    /*-webkit-box-shadow: 0 1px -1px 1px #ccc;*/
    /*box-shadow: 0 1px -1px 1px #ccc;*/
    -webkit-box-shadow: 0 0 10px #bfbbbb;
    box-shadow: 0 0 10px #bfbbbb;
    /*margin: 6px;*/
  }
  .product-inner:hover img {
    transform: scale(1.2);
  }
  .m-10 {
    margin: 10px;
  }
  .slick-slide {
    width: 269px !important;
      margin-left: 3px;
      margin-right: 5px;
  }
  .carousel-caption {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
    bottom: 0px !important;
  }
  .carousel-caption h5 {
    font-weight: 400;
    text-transform: capitalize;
    font-size: 20px;
  }
  .capitalize {
    text-transform: capitalize;
  }
  .container-custom {
    max-width: 1750px;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
    .bg-about-us{
        background-color: #2c2c2c;
        color: #FFFFFF;
    }
    .section-about-us .description {
        color: #FFFFFF !important;
    }
</style>



